<template>
  <div class="item row">
      <div class="col-2 id">
        {{data.id}}
      </div>
       <div class="col-5 name">
        {{data.name}}
      </div>
       <div class="col-1 quantity">
         {{data.quantity}}
      </div>
       <div class="col-2 price">
        {{data.price}}
      </div>
       <div class="col-2 delete">
        <button @click="removeItem" class="btn btn-danger btn-block">X</button>
      </div>
  </div>
</template>

<script>
export default {
  props:{
    data:{
      type: Object,
      required: true
    }
  },
  methods:{
    removeItem(){
      this.$emit('removeItemEvent', this.data.id);
    }
  }
}
</script>

