import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    items:[
      
    ]
  },
  getters:{
    items: state => state.items,
    lastItem: state => state.items.slice(-1)[0]
  },
  mutations: {
    ADD_ITEM_IN_ITEMS(state, value){
      state.items.push(value)
    },
    REMOVE_ITEM_FROM_ITEMS(state, id){
      const position = state.items.findIndex((item) => item.id == id)
      state.items.splice(position, 1)
    },
    REMOVE_ALL_FROM_ALLITEMS(state){
      state.items = []
    }
  },
  actions:{
    addItem({state, commit, dispatch}, value){
      commit('ADD_ITEM_IN_ITEMS', value);
    },
    //dispatch apelarea unei actiuni
    //commit apelare de mutation
    removeItem({state, commit, dispatch}, id){
      commit('REMOVE_ITEM_FROM_ITEMS', id)
    },
    removeAllItems({state, commit, dispatch}){
      commit('REMOVE_ALL_FROM_ALLITEMS');
    }
  }
})