<template>
  <div class="shopping-list">
    <v-title />
    <div v-if="showWarning" class="alert alert-danger">Insert an item name</div>
    <div class="addItem">
      <div class="row form-group">
        <div class="col-10">
          <input type="text" v-model="itemName" v-on:keyup.enter="addItem" class="form-control" id />
        </div>
        <div class="col-2">
          <button @click="addItem" class="btn btn-primary btn-block">Add</button>
        </div>
      </div>
    </div>
    <hr>
    <div v-if="items.length" class="list">
      <div class="item row">
        <div class="col-2 id">ID</div>
        <div class="col-5 name">NAME</div>
        <div class="col-1 quantity">QUANTITY</div>
        <div class="col-2 price">PRICE</div>
        <div class="col-2 delete">
          <button @click="removeAll" class="btn btn-dark btn-block">CLEAR</button>
        </div>
      </div>
      <hr>
      <v-list-item @removeItemEvent="removeItem"   v-for="(item, index) in items" :data="item" :key="index" />
      <hr>
      <v-total :data="items" />
    </div>
    <div v-else class="noItem alert alert-warning">Please add items</div>
    <hr>
    <v-footer />
  </div>
</template>

<script>
import Title from "./title";
import Footer from "./footer";
import ListItem from "./list-item";
import Total from "./total";

import { mapGetters } from "vuex";

export default {
  data() {
    return {
      showWarning: false,
      itemName: ""
    };
  },
  components: {
    vTitle: Title,
    vFooter: Footer,
    vListItem: ListItem,
    vTotal: Total
  },
  computed: {
    ...mapGetters(["items", "lastItem"])
  },
  methods: {
    addItem() {
      if (this.itemName.trim().length === 0) {
        //show user that it has an invalid item name
        this.showWarning = true;
        return;
      }
      //clear warning if everything is ok
      this.showWarning = false;
      let itemId =
        this.lastItem && "id" in this.lastItem ? this.lastItem.id : 0;
      ++itemId;

      this.$store.dispatch("addItem", {
        id: itemId,
        name: this.itemName,
        quantity: 1,
        price: Math.ceil(Math.random() * 100)
      });
      this.itemName = "";
    },
    removeItem(value) {
      //de aici nu te atinig de commits sau states, ci doar de actiuni pe care le folosesti prin dispatch si getters
      this.$store.dispatch('removeItem', value);
    },
    removeAll(){
        this.$store.dispatch('removeAllItems')
    },
    
  }
};
</script>

