<template>
  <div class="item row">
      <div class="col-8 total">
        TOTAL
      </div>
       <div class="col-2 amount">
        {{amount}}
      </div>
       <div class="col-2 currency">
         LEI
      </div>
       
  </div>
</template>

<script>
export default {
  props:{
      data:{
        type:Array,
        required: true
      }
  },
  //computed iti intoarce valoarea cache-uita
  computed:{
    amount(){
      return this.data.reduce((total, item) => total+item.price, 0)
    }
  }
}
</script>

